body {
    /* height: 100%; */
    font-family: apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto,
        "Helvetica Neue", "Fira Sans", Ubuntu, Oxygen, "Oxygen Sans", Cantarell,
        "Droid Sans", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Emoji",
        "Segoe UI Symbol", "Lucida Grande", Helvetica, Arial, sans-serif;
}
#root {
    overflow: hidden;
}

.social-links {
    max-width: 450px;
}

.btn-facebook {
    color: #fff;
    background-color: #3b5998;
    transition-duration: 300ms;
}
.btn-facebook:hover {
    color: #fff;
    background-color: #3a61b3;
}

.btn-black {
    color: #fff;
    background-color: #000;
    transition-duration: 300ms;
}

.btn-black:hover {
    color: #fff;
    background-color: #333;
}
.btn-white-black {
    color: #000;
    background-color: #fff;
    transition-duration: 300ms;
}

.btn-white-black:hover {
    color: #fff;
    background-color: #616467;
}
.btn-black-white {
    color: rgba(255, 255, 255, 1);
    background-color: #000;
    transition-duration: 300ms;
}

.btn-black-white:hover {
    background-color: rgba(255, 255, 255, 1);
    color: #000;
}

.btn-google {
    color: #616467;
    box-shadow: inset 0 0 0 2px #616467;
    background-color: transparent;
    transition-duration: 300ms;
}

.btn-google:hover {
    border-color: #adadad;
    color: #fff;
    background-color: #616467;
}

.btn-submit {
    color: #fff;
    background-color: #15883e;
    transition-duration: 300ms;
}

.btn-submit:hover {
    color: #fff;
    background-color: #1db954;
}
.btn-facebook-2 {
    color: #fff;
    background-color: #1877f2;
    transition-duration: all 33ms ease-in-out;
}

.btn-facebook-2:hover {
    color: #fff;
    background-color: #4893f5 !important;
    transform: scale(1.04);
}

.btn-nav {
    font-size: 15px;
    border-radius: 5px;
    color: rgba(255, 255, 255, 0.5);
}

.btn-nav:hover {
    background-color: #282828;
    color: #fff;
}

.bg-gray {
    background-color: #282828;
}

.list-group .active {
    color: #fff !important;
}

.btn-register {
    color: #000;
    background-color: #1ed760;
    transition-duration: all 33ms ease-in-out;
}

.btn-register:hover {
    color: #000;
    background-color: #21e065 !important;
    transform: scale(1.04);
}

.no-focus:focus {
    box-shadow: none;
    outline: none;
}

.divider {
    display: flex;
    align-items: center;
    text-align: center;
}

/* To show the lines on right 
  and left sides of the text */
.divider::after,
.divider::before {
    content: "";
    border: 1px solid #d9dadc;
    flex: 1;
}

/* Space on left and right sides of text */
.divider:not(:empty)::before {
    margin-right: 0.9em;
}

.divider:not(:empty)::after {
    margin-left: 0.9em;
}

.forget-text {
    color: #616467;
}

.forget-text:hover {
    color: #1da64d;
}

.text-gray {
    color: #616467;
}

.text-green {
    color: rgb(29, 185, 84);
}

a.text-green:hover {
    color: rgb(30, 215, 96);
}

/* SIdebar */

.navbar {
    width: 250px;
    position: fixed;
    margin-left: -300px;
    background-color: #7952b3;
    transition: 0.4s;
}

.my-container {
    transition: 0.4s;
    height: 100%;
}

.bg-gray {
    background-color: #1e1e1e;
}

.bg-gray-50 {
    background-color: #181818;
}

.bg-gray-50:hover {
    background-color: #282828;
}

.active-nav {
    margin-left: 0px;
}

a.muted-text:hover {
    color: rgba(255, 255, 255, 0.9);
}

.active-cont {
    margin-left: 250px;
}

.no-box-shadow,
.no-box-shadow:focus,
.no-box-shadow:active,
.no-box-shadow:hover {
    box-shadow: none;
}

.drop-item {
    padding: 10px 10px 10px 10px;
    cursor: pointer;
    border-radius: 15px;
    color: rgba(255, 255, 255, 0.5);
}

.drop-item:hover {
    background-color: #282828;
    color: #fff;
}

.gray-btn {
    background-color: #000;
    transition: 4ms;
}

.gray-btn:hover {
    background-color: #282828;
}

.muted-text {
    color: rgba(255, 255, 255, 0.5);
}

.truncate-overflow-1 {
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.truncate-overflow-2 {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.truncate-overflow-3 {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.close-btn:hover {
    color: #4653eb;

    transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
}

.close-btn {
    color: #6270fb;
    transition: opacity 300ms ease,
        transform 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275),
        -webkit-transform 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.gray-text {
    color: rgba(255, 255, 255, 0.6);
}

a.gray-text:hover {
    color: rgba(255, 255, 255, 0.3);
}

.bg {
    background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0, 1));
}

.play-btn:hover {
    color: #fff;
    font-size: 22px !important;
}

/* MUSIC SLIDER */

.slider-cont {
    --progress-bar-height: 6px;
    --thumb-width: 20px;
    --thumb-height: 20px;
}

.slider-cont::before {
    content: "";
    background-color: #fff;
    width: 100%;
    height: var(--progress-bar-height);
    display: block;
    position: absolute;
    /* border-radius: 10px; */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
    opacity: 1;
}

.music-slider {
    height: 4px;
    width: 100%;
    background-color: #fff;
    -webkit-appearance: none;
    opacity: 0;
    margin: 0;
    outline: none;
}

.music-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    background-color: #4653eb;
    border-radius: 50%;
    cursor: pointer;
    margin: 0;
}

.thumb {
    width: var(--thumb-width);
    height: var(--thumb-height);
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.753);
    z-index: 3;
    background: rgba(255, 255, 255, 1);
    position: absolute;
    border-radius: 50%;
    top: 50%;
    transform: translate(0%, -50%);
    pointer-events: none;
    user-select: none;
}

.progress-bar-cover {
    width: 100%;
    height: var(--progress-bar-height);
    display: block;
    outline: none;
    z-index: 1;
    background: #4653eb;
    position: absolute;
    /* border-radius: 10px; */
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
    user-select: none;
}

.volume-slider {
    height: 4px;
    width: 100%;
    background-color: #fff;
    -webkit-appearance: none;
    /* opacity: 0; */
    margin: 0;
    outline: none;
    margin-bottom: 15px;
    border-radius: 10px;
}

.volume-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 20px;
    width: 20px;
    background-color: #4653eb;
    border-radius: 50%;
    cursor: pointer;
    margin: 0;
}

/* Search */
.search-input {
    box-shadow: none;
    outline: none;
    border-radius: 20px;
    border: 0;
    width: 90%;
    height: 40px;
    margin: auto auto;
}

.search-input:focus {
    border: 0;
    box-shadow: none;
}
